import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Col, Collapse, Row} from 'antd';
import PropTypes from "prop-types";
import {fetchProfileInfo, fetchRegister, fetchSessionParameters, fetchTool} from '../../redux/lince/videoActions';
import basicStyle from "@iso/assets/styles/constants";
import CodeMirror from "../../containers/Lince/styled/CodeMirror.styles";


class DataInspector extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentWillMount() {
        this.props.fetchProfileInfo();
        this.props.fetchSessionParameters();
        this.props.fetchRegister();
        this.props.fetchTool();
    }

    render() {
        const { Panel } = Collapse;
        let users = []/*, register = []*/;
        if (this.props.profile.userProfiles) {
            users = this.props.profile.userProfiles;
        }
        console.log("render");
        console.log(this.props.sessionParams);
        console.log(this.props.profile);
        const {rowStyle, colStyle, gutter} = basicStyle;
        return (
            <div>
                <Row style={rowStyle} gutter={gutter} justify="start">
                    <Col md={12} sm={12} xs={24} style={colStyle}>
                        <div>
                            <h4>Users</h4>
                            <ul className="media-list list-items">
                                {users.map((v, index) => {
                                    return <li key={`user${index}`}>
                                        <span>Observador {index}:</span>
                                        <pre>{JSON.stringify(v, null, 2)}</pre>
                                    </li>
                                })}
                            </ul>
                        </div>
                    </Col>
                    <Col md={12} sm={12} xs={24} style={colStyle}>
                        <div>
                            <h4>Tool</h4>
                            <Collapse defaultActiveKey={['1']}>
                                {this.props.tool.map((i, index) => {
                                    return <Panel key={`tool${index}`} header={`Tool ${index}`}>
                                        <CodeMirror value={JSON.stringify(i, null, 2)}
                                                    options={{
                                                        lineNumbers: true,
                                                        readOnly: false,
                                                        tabSize: 4,
                                                        mode: 'r',
                                                        theme: 'zenburn',
                                                    }}/>
                                    </Panel>
                                })}
                            </Collapse>

                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col md={24} sm={24} xs={24} style={colStyle}>
                        <Collapse defaultActiveKey={['1']}>
                            {this.props.register.map((i, index) => {
                                return <Panel key={`register${index}`} header={`Register ${index}`}>
                                    <CodeMirror value={JSON.stringify(i, null, 2)}
                                                options={{
                                                    lineNumbers: true,
                                                    readOnly: false,
                                                    tabSize: 4,
                                                    mode: 'r',
                                                    theme: 'zenburn',
                                                }}/>
                                </Panel>
                            })}
                        </Collapse>
                    </Col>
                </Row>
            </div>
        );
    }
}

DataInspector.propTypes = {
    sessionParams: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired,
    register: PropTypes.array,
    tool: PropTypes.array,
    fetchSessionParameters: PropTypes.func.isRequired,
    fetchProfileInfo: PropTypes.func.isRequired,
    fetchRegister: PropTypes.func.isRequired,
    fetchTool: PropTypes.func.isRequired,
};
const mapStateToProps = state => ({
    sessionParams: state.lince.sessionParams,
    profile: state.lince.profile,
    register: state.lince.register,
    tool: state.lince.tool,
});

export default connect(mapStateToProps, {
    fetchSessionParameters,
    fetchProfileInfo,
    fetchRegister,
    fetchTool
})(DataInspector);